import 'ol/ol.css';
import GeoTIFF from 'ol/source/GeoTIFF';
import Map from 'ol/Map';
import TileLayer from 'ol/layer/WebGLTile';
import {OSM, TileDebug} from 'ol/source';
import View from 'ol/View'
import MapLibreLayer from '@geoblocks/ol-maplibre-layer';
import proj4 from 'proj4';
import * as olProj from 'ol/proj';

const hs = {
  "version": 8,
  "sources": {
      "hs_raster": {
              "type": "raster",
              "tiles": ["https://xycarto-base-maps.s3.ap-southeast-2.amazonaws.com/tile-service-raster/tiles/hs-nz/{z}/{x}/{y}.png"],
              "bounds": [-178.826516,-52.620881,179.067794,-29.231342],
              "scheme": "tms",
              "tileSize": 256,
      "attribution": "Mine!",
      "maxzoom": 14
      }
  },
  "layers":[ 
      {
          "id": "hs_raster",
          "source": "hs_raster",
          "type": "raster",
          "layout": { "visibility": "visible" },
          "paint": {
              "raster-opacity": 0.75,
              "raster-resampling": "linear",
              "raster-brightness-max": 1.0,
              "raster-brightness-min": 0.5,
              "raster-contrast": 0,
              "raster-opacity": {
                  "stops": [
                    [1, 0.35],
                    [7, 0.35],
                    [8, 0.65],
                    [15, 0.65],
                    [16, 0.3]
                  ]
                },
          }
      }
  ]
}

const url = 'https://xycarto-base-maps.s3.ap-southeast-2.amazonaws.com/tile-service-cog/webmerdem_cog.tif'

const source = new GeoTIFF({
  sources: [
    {
      url: url,
      max: 1000
    }
  ],
});

const rendered = new TileLayer({
  opacity: 0.5,
  source: source,
  style: {
    color: [
      'interpolate',
      ['linear'],
      ['band', 1],
      0, [0, 0, 0, 0],
      0.01, [101, 145, 102, 0.35],
      0.15, [116, 158, 117, 1],
      0.75, [223, 230, 220, 1]
    ],
   }
})

const libre = new MapLibreLayer({  
  opacity: 1,
  crossOrigin: 'anonymous',
  maplibreOptions: {
    style: './style.json',
  }
});

const hillshade =  new MapLibreLayer({  
  crossOrigin: 'anonymous',
  maplibreOptions: {
    style: hs,
  }
});

const map = new Map({
  target: 'map',
  layers: [rendered, hillshade, libre],
  view: new View({
    center: olProj.fromLonLat([174.0,-41.29]),
    zoom: 8
  })
});